import * as yup from 'yup'

export const schemaYup = yup.object().shape({
  // cedula: yup.string()
  //   .required('La Cdula es obligatoria')
  //   .length(11, 'Debe tener exactamente 11 dgitos'),
  
  // nombreC: yup.string().required('El Nombre es obligatorio'),
  email: yup.string().trim()
    .required('El Correo Electrnico es obligatorio')
    .email('El Correo Electrnico no es vlido'),
  phone: yup.string()
    .required('El Telfono es obligatorio')
    .length(10, 'Debe tener exactamente 10 dgitos'),
  institucion: yup.string().required('La Institucin es obligatoria'),
  // permisos: yup.array()
  //   .min(1, 'Debes seleccionar al menos 1 Permiso')
  //   .required('El Permiso es obligatorio'),
  region: yup.string().required('La Regin es obligatoria'),
  provincia: yup.string().required('La Provincia es obligatoria'),
  municipio: yup.string().required('El Municipio es obligatorio'),
  distrito: yup.string().required('El Distrito es obligatorio'),
  password: yup.string(),
    // .required('La Contrasea es obligatoria'),
  cPassword: yup.string()
    // .required('La Contrasea es obligatoria')
    .oneOf([yup.ref('password')], 'Las contraseas no coinciden'),
})